import React, { useState, useEffect, Fragment, useRef } from "react";
import pokedex from '../assets/pokedex.png'
const ListaPokemones = () => {
  let refBuscador = useRef()
  const [pokemones, setPokemones] = useState([]);
  const [pokemon, setPokemon] = useState(null);
  const [loading, setLoading] = useState(true);
  const [posiblesBusquedas, setPosiblesBusquedas] = useState([]);

  useEffect(() => {
    const getPokemon = async () => {
      let url = "https://pokeapi.co/api/v2/pokemon?offset=0&limit=100";
      let pokemons = [];
      let json = { next: 2 };
      while (json.next != null) {
        try {
          let res = await fetch(url);
          json = await res.json();
          console.log(json);
          json.results.forEach(async (element) => {
            try {
              let res = await fetch(element.url),
                json_pokemon = await res.json();

              let img = (
                <img src={json_pokemon.sprites.front_default} alt="pokemon" />
              );
              let poke = {
                name: element.name,
                avatar: img,
              };
              pokemons.push(poke);
            } catch (e) {
              console.log(e);
              console.log(element);
              alert(
                "Caractrísticas del pokemón " +
                  element.name +
                  "  no encontrado en la PokeApi; 404 NOT FOUND"
              );
            }
          });

          url = json.next;

          setPokemones((p) => pokemons);
          if (json.next == null) {
            setLoading(false);
          }
        } catch (e) {
          console.log(e);
        }
      }
    };

    getPokemon();
  }, []);

  const handleInput = (e) => {
    let nombre = e.target.value.toLowerCase();
    let pokemon_encontrado = false;
    if (nombre !== "") {
 
      for (let i = 0; i < pokemones.length; i++) {
          if (pokemones[i].name === nombre) {
            if(posiblesBusquedas.length === 1){
              setPokemon(pokemones[i]);
              setPosiblesBusquedas([])
            }
            
            if(posiblesBusquedas.length <  1){
              setPosiblesBusquedas([])
            }
            pokemon_encontrado = true
            break;
          }

        }
      
     if(!pokemon_encontrado){
      let pokemones_validos = [];
      pokemones.forEach((e) => {
        let valido = true;
        for (let i = 0; i < nombre.length; i++) {
          if (nombre.charAt(i) !== e.name.charAt(i)) {
            valido = false;
          }
        }
        if (valido) {
          pokemones_validos.push(e);
        }
      });
      setPosiblesBusquedas(pokemones_validos);
      console.log(posiblesBusquedas);
     }
    } else {
      setPosiblesBusquedas([]);
      setPokemon(null);
    }
  };

  const primeraLetraMayuscula = (cadena) => {
    const primerCaracter = cadena.charAt(0).toUpperCase();
    const restoDeLaCadena = cadena.substring(1, cadena.length);
    return primerCaracter.concat(restoDeLaCadena);
  };

  const seleccionarPokemon = (pokemon_seleccionado) => {console.log(pokemon_seleccionado)
    setPokemon(pokemon_seleccionado)
    setPosiblesBusquedas([])
    refBuscador.current.value = pokemon_seleccionado.name;
  };
  return (
    <Fragment>
      {loading && (
        <div className="container-loader">
          <div className="loader"></div>
  
        </div>
      )}

      <div className="container">
      <div className="title-container">
      <div className="pokedex"> <img src={pokedex} alt="pokedex"/></div>
        <h1>Busca tu pokemón!</h1>

      </div>

        <input
          placeholder=" Pokemon"
          type="search"
          onChange={handleInput}
          onClick={handleInput}
          ref={refBuscador}
        />
        {posiblesBusquedas.length !== 0 && (
          <div className="lista-busquedas">
            {posiblesBusquedas.sort().map((p) => (
              <PokemonSugerido
                myOnClick={()=>{seleccionarPokemon(p)}}
                key={p.name}
                pokemon={p}
              ></PokemonSugerido>
            ))}
          </div>
        )}

        {pokemon !== null && (
          <article>
            <figure>
              {pokemon.avatar}
              <figcaption>
                <p>{primeraLetraMayuscula(pokemon.name)}</p>
              </figcaption>
            </figure>
          </article>
        )}
      </div>
    </Fragment>
  );
};

const PokemonSugerido = ({ pokemon,myOnClick }) => {
  return (
    <li id={pokemon.name} onClick={myOnClick}>
      <p>{pokemon.name}</p> <span>{pokemon.avatar}</span>
    </li>
  );
};
export default ListaPokemones;
